import React, { useState } from "react";
import { Form, Input, Button, Switch, Collapse } from "antd";
import styled from "styled-components";
import axios from "../../../node_modules/axios/index";

const ShopRegFormContainer = ({ history }) => {
  const ErrorMessage = styled.div`
    color: red;
    text-align: center;
    font-size: 0.875rem;
    margin-top: 1rem;
  `;

  const { Panel } = Collapse;
  function callback(key) {
    // console.log(key);
  }

  const [form, setForm] = useState({
    shopName: "",
    representative: "",
    crn: "",
    telnum: "",
    freepee: "",
    nofree: "",
    priPhone: "",
    csPhone: "",
    csTime: "",
    toPanda: "",
    reship: "",
    returnpee: "",
    tradepee: "",
    returnaddress: "",
    candate: "",
    noreturn: "",
    comaddress: "",
    avdtime: "",
  });
  const {
    shopName,
    representative,
    crn,
    telnum,
    freepee,
    nofree,
    priPhone,
    csPhone,
    csTime,
    toPanda,
    reship,
    returnpee,
    tradepee,
    returnaddress,
    candate,
    noreturn,
    comaddress,
    avdtime,
  } = form;

  const onChangeF = (e) => {
    const nextForm = {
      ...form,
      [e.target.name]: e.target.value,
    };
    setForm(nextForm);
  };

  const onClick = async (e) => {
    try {
      const body = {
        shopName: shopName,
        representative: representative,
        crn: crn,
        telnum: telnum,
        freepee: freepee,
        nofree: nofree,
        priPhone: priPhone,
        csPhone: csPhone,
        csTime: csTime,
        toPanda: toPanda,
        reship: reship,
        returnpee: returnpee,
        tradepee: tradepee,
        returnaddress: returnaddress,
        candate: candate,
        noreturn: noreturn,
        tagree: e.Termsagree,
        iagree: e.Infoagree,
        comaddress: comaddress,
        avdtime: avdtime,
      };
      // console.log("");
      // console.log(body);

      if (
        !shopName ||
        !representative ||
        !crn ||
        !telnum ||
        !freepee ||
        !nofree ||
        !priPhone ||
        !csPhone ||
        !csTime ||
        !toPanda ||
        !reship ||
        !returnpee ||
        !tradepee ||
        !returnaddress ||
        !candate ||
        !noreturn ||
        !comaddress ||
        !avdtime
      ) {
        alert("  ");
        return;
      }

      if (e.Termsagree === false || e.Infoagree === false) {
        alert(",  ");
        return;
      }
      await axios.post("/createShop", body).then((response) => {
        if (response.data.success) {
          alert(" !");
        } else {
          alert(
            " ,    "
          );
        }
      });
    } catch (e) {
      // console.log(e);
    }
  };

  // async function onSubmit(e) {
  //   // console.log("");
  //   // console.log(e);
  //   if (
  //     !shopName ||
  //     !representative ||
  //     !crn ||
  //     !telnum ||
  //     !freepee ||
  //     !nofree ||
  //     !priPhone ||
  //     !csPhone ||
  //     !csTime ||
  //     !toPanda ||
  //     !reship ||
  //     !returnpee ||
  //     !tradepee ||
  //     !returnaddress ||
  //     !candate ||
  //     !noreturn
  //   ) {
  //     alert("  ");
  //   }

  //   if (e.Termsagree === false || e.Infoagree === false) {
  //     alert(",  ");
  //   }

  //   const body = {
  //     shopName: shopName,
  //     representative: representative,
  //     crn: crn,
  //     telnum: telnum,
  //     freepee: freepee,
  //     nofree: nofree,
  //     priPhone: priPhone,
  //     csPhone: csPhone,
  //     csTime: csTime,
  //     toPanda: toPanda,
  //     reship: reship,
  //     returnpee: returnpee,
  //     tradepee: tradepee,
  //     returnaddress: returnaddress,
  //     candate: candate,
  //     noreturn: noreturn,
  //     Termsagree: e.Termsagree,
  //     Infoagree: e.Termsagree,
  //   };
  //   // console.log("body");
  //   const data = await body;
  //   // console.log(data);
  //   // console.log(e.Termsagree);

  //   await axios.post("/createShop", data).then((response) => {
  //     if (response.data.success) {
  //       alert(" !");
  //     } else {
  //       alert(" ");
  //     }
  //   });
  // }

  const text = `
    A dog is a type of domesticated animal.
    Known for its loyalty and faithfulness,
    it can be found as a welcome guest in many households across the world.
  `;
  return (
    <>
      <br />
      <br />
      <br />

      <div style={{ display: "flex", justifyContent: "center" }}>
        <Collapse defaultActiveKey={["1"]} onChange={callback}>
          <Panel header="" key="1">
            <p>{text}</p>
          </Panel>
          <Panel header="" key="2">
            <p>{text}</p>
          </Panel>
          <Panel header="" key="3">
            <p>{text}</p>
          </Panel>
        </Collapse>
      </div>

      <br />
      <br />

      <Form
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 14,
        }}
        layout="horizontal"
        onFinish={onClick}
      >
        <Form.Item
          label=" "
          // name="Termsagree"
          valuePropName="Termsagree"
        >
          <Switch />
        </Form.Item>
        <h1>  </h1>
        <Form.Item label="">
          <Input
            type={"text"}
            name="shopName"
            onChange={onChangeF}
            placeholder="  "
          />
        </Form.Item>
        <Form.Item label="">
          <Input
            type={"text"}
            name="representative"
            onChange={onChangeF}
            placeholder="  "
          />
        </Form.Item>

        <Form.Item label="">
          <Input
            type={"text"}
            name="crn"
            onChange={onChangeF}
            placeholder="  "
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={"text"}
            name="telnum"
            onChange={onChangeF}
            placeholder="  "
          />
        </Form.Item>
        <Form.Item label="  ">
          <Input
            type={"number"}
            name="freepee"
            onChange={onChangeF}
            placeholder="      ,      "
          />
        </Form.Item>

        <Form.Item label="   ">
          <Input
            type={"number"}
            name="nofree"
            onChange={onChangeF}
            placeholder="    "
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={text}
            name="priPhone"
            onChange={onChangeF}
            placeholder="  () ( x)"
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={text}
            name="csPhone"
            onChange={onChangeF}
            placeholder="     "
          />
        </Form.Item>
        <Form.Item label=" ">
          <Input
            type={text}
            name="comaddress"
            onChange={onChangeF}
            placeholder="    "
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={text}
            name="csTime"
            onChange={onChangeF}
            placeholder="     "
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={text}
            name="avdtime"
            onChange={onChangeF}
            placeholder="     "
          />
        </Form.Item>
        <Form.Item
          label=" "
          // name="Infoagree"
          valuePropName="Infoagree"
        >
          <Switch />
        </Form.Item>

        <Form.Item
          name="toPanda"
          onChange={onChangeF}
          label="To "
          rules={[
            {
              required: true,
              message:
                "()   ex) , ",
            },
          ]}
        >
          <Input.TextArea
            showCount
            maxLength={250}
            name="toPanda"
            onChange={onChangeF}
          />
        </Form.Item>

        <h1>  </h1>
        {/*    */}

        <Form.Item label="  ">
          <Input
            type={text}
            name="reship"
            onChange={onChangeF}
            placeholder="  "
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={"number"}
            name="returnpee"
            onChange={onChangeF}
            placeholder="3000"
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={"number"}
            name="tradepee"
            onChange={onChangeF}
            placeholder="   "
          />
        </Form.Item>

        <Form.Item label=" ">
          <Input
            type={text}
            name="returnaddress"
            onChange={onChangeF}
            placeholder="   "
          />
        </Form.Item>
        <h3>/     </h3>
        <Form.Item
          name="candate"
          onChange={onChangeF}
          label=""
          rules={[
            {
              required: true,
              message: "/     ",
            },
          ]}
        >
          <Input.TextArea
            showCount
            maxLength={250}
            name="candate"
            onChange={onChangeF}
          />
        </Form.Item>

        <Form.Item
          name="noreturn"
          onChange={onChangeF}
          label="   "
          rules={[
            {
              required: true,
              message: "   ",
            },
          ]}
        >
          <Input.TextArea
            showCount
            maxLength={500}
            name="noreturn"
            onChange={onChangeF}
          />
        </Form.Item>

        <Form.Item label="">
          <Button type="primary" htmlType="submit">
            submit
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default ShopRegFormContainer;
